const EPOCHS = {
    background: ['url(#dots)', 'url(#dots2)'],
    labels: {
        fill: '#444',
        fontFamily: 'Sine Mono, monospace',
        fontSize: {
            S: 8,
            M: 10,
            L: 12,
            XL: 12,
            XXL: 14,
            XXXL: 14,
        },
        fontWeight: 400,
    },
    stroke: '#444',
    strokeDasharray: '0',
    strokeWidth: 0,
};

const CONNECTORS = {
    fill: 'transparent',
    stroke: 'transparent',
    strokeDasharray: '0',
    strokeWidth: 0,
};

class Timeline extends Component {

    constructor(props) {
        super(props);

        this.createCSS = this.createCSS.bind(this);
        this.findDetails = this.findDetails.bind(this);
        this.findLogo = this.findLogo.bind(this);
        this.renderLogos = this.renderLogos.bind(this);
        this.selectLine = this.selectLine.bind(this);
        this.viewbox = this.viewbox.bind(this);

        this.lineHeight = 1;
        this.totalLineHeight = 3;

        this.data = {
            lines: [],
            connectors: [],
            logos: [],
        }

        this.state = {
            partiesList: [],
            selectedLine: 0,
            size: 'S',
            styles: '',
            svg: {
                connectors: [],
                epochs: [],
                ghosts: [],
                labels: [],
                lines: [],
                logos: [],
                rays: [],
                visible: false,
            },
        };
        Promise.all([
            dsv(';', lines),
            dsv(';', connectors),
            dsv(';', logos),
        ]).then(
            files => {
                this.data.lines = files[0];
                this.data.connectors = files[1];
                this.data.logos = files[2];
                const partiesList = this.data.logos.sort((a,b) => parseInt(a.YearFrom, 10) - parseInt(b.YearFrom, 10)).map(d => ({
                    logo: d.Logo,
                    name: `${d.YearFrom} - ${d.Label}`,
                    value: d.Line,
                }));
                this.setState({partiesList}, this.viewbox );
            }
        );
    }

    componentDidMount() {
        window.addEventListener('resize', this.viewbox, false);
        this.viewbox();
    }

    createCSS(size) {
        let styles = `
        .Timeline:not(.Timeline-selected-line-0) .timeline-chart .arc-alive .line-alive {
            fill: ${LINES.fill.inactive[size]};
        }`;
        const lines = this.data.lines.map(d => parseInt(d.Line, 10));
        const maxLine = Math.max(...lines);
        for (let i=1; i<= maxLine; i+=1) {
            styles += `
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-alive.l${i} .line-alive {
              fill: ${LINES.fill.active[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.l${i} .line-ghost {
              fill: ${LINES.fill.ghost[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.f${i} .line-ghost {
                fill: ${LINES.fill.ghostSiblingFrom[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.t${i} .line-ghost {
                fill: ${LINES.fill.ghostSiblingTo[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.t${i}.f${i} .line-ghost {
                fill: ${LINES.fill.ghostSiblingToFrom[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-alive.f${i} .line-alive {
                fill: ${LINES.fill.siblingFrom[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-alive.t${i} .line-alive {
                fill: ${LINES.fill.siblingTo[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-alive.t${i}.f${i} .line-alive {
                fill: ${LINES.fill.siblingToFrom[size]};
            }
            @media screen and (min-width: 768px) {
                .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.l${i} .line-label,
                .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.f${i} .line-label,
                .Timeline.Timeline-selected-line-${i} .timeline-chart .arc-ghost.t${i} .line-label {
                    display: block;
                }
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .connector.t${i} .connector-arrow {
                fill: ${LINES.fill.siblingFrom[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .connector.f${i} .connector-arrow {
                fill: ${LINES.fill.active[size]};
            }
            .Timeline.Timeline-selected-line-${i} .timeline-chart .connector.f${i}.t${i} .connector-arrow {
                fill: ${LINES.fill.siblingToFrom[size]};
            }`;
        }
        return styles;
    }

    findDetails() {
        const lineID = this.state.selectedLine;
        if (lineID && lineID !== 0 && this.data.logos.length > 0) {
            return {
                selectedLogos: this.data.logos.filter(d => d.Line === lineID),
                selectedHistory: parties.history[lineID] || null,
            };
        }
        return {
            selectedLogos: [],
            selectedHistory: null,
        }
    }

    findLogo() {
        const lineID = this.state.selectedLine;
        if (lineID && lineID !== 0 && this.data.logos.length > 0) {
            const logos = this.data.logos.filter(d => d.Line === lineID);
            if (logos.length > 0) {
                const selected = logos[Math.floor(Math.random() * logos.length)];
                return { logo: `${ASSETS_URL}/${selected.Logo}.png`, year: selected.YearFrom, label: selected.Label };
            }
        }
        return { logo: `${ASSETS_URL}/default.png`, year: '', label: '' };
    }

    renderLogos(props, option, snapshot, className) {
        const imgStyle = {
            borderRadius: '50%',
            marginRight: 10,
            objectFit: 'contain',
            verticalAlign: 'middle',
        };
        return (
            <button {...props} className={className} type="button">
                <span><img alt="" style={imgStyle} width="32" height="32" src={`/assets/logos/${option.logo}.png`} /><span>{option.name}</span></span>
            </button>
        );
    }

    selectLine(id) {
        this.setState({selectedLine: id});
        document.querySelector('.Timeline').scrollIntoView();
    }

    viewbox() {
        console.log(this.data);
        let size = 'S';
        if (window.matchMedia('screen and (min-width:1800px)').matches) {
            size = 'XXXL';
        } else if (window.matchMedia('screen and (min-width:1460px)').matches) {
            size = 'XXL';
        } else if (window.matchMedia('screen and (min-width:1280px)').matches) {
            size = 'XL';
        } else if (window.matchMedia('screen and (min-width:1024px)').matches) {
            size = 'L';
        } else if (window.matchMedia('screen and (min-width:768px)').matches) {
            size = 'M';
        }
        console.log('Size', size);
        const styles = this.createCSS(size);
        if (this.data.lines.length > 0 && this.data.connectors.length > 0 && this.data.logos.length > 0) {
            // Years
            let rawYears = this.data.lines.map( item => parseInt(item.YearFrom,10) );
            // Add events and elections
            rawYears = rawYears.concat(this.data.lines.map( item => parseInt(item.YearTo,10) ))
                .concat(events.list.map( item => item.year ))
                .concat(this.data.connectors.map( item => item.Year ));
            rawYears.push(MAX_YEAR)
            rawYears.push(MIN_YEAR);
            rawYears = rawYears.filter( (val, index) => rawYears.indexOf(val) === index )
                .sort();
            // Add a Ray for each valid year
            let i = 0;
            const rays = [];
            const epochsAreas = [];
            const lines = [];
            const ghosts = [];
            const connectors = [];
            const radius = RAYS.hole[size] + (MAX_RADIUS[size] * LINE_HEIGHT[size]);
            // Areas of important epochs
            epochs.list.forEach((e, i) => {
                epochsAreas.push({
                    key: `epoch-${e.from}`,
                    fill: EPOCHS.background[i % 2],
                    from: (START_ANGLE + ((e.from - MIN_YEAR) * YEAR_ANGLE)),
                    index: i,
                    innerRadius: RAYS.hole[size],
                    label: {
                        fill: EPOCHS.labels.fill,
                        fontFamily: EPOCHS.labels.fontFamily,
                        fontSize: EPOCHS.labels.fontSize[size],
                        fontWeight: EPOCHS.labels.fontWeight,
                        show: true,
                        text: e.title,
                    },
                    outerRadius: radius + (i%2 === 0 ? 0 : LINE_HEIGHT[size] * 2),
                    size,
                    stroke: EPOCHS.stroke,
                    strokeWidth: EPOCHS.strokeWidth,
                    strokeDasharray: EPOCHS.strokeDasharray, 
                    to: (START_ANGLE + ((e.to - MIN_YEAR) * YEAR_ANGLE)),
                });
            });
            // Lines and ghosts
            const raysLength = {};
            const addedGhosts = [];
            this.data.lines.forEach((l) => {
                let counter = parseInt(l.YearFrom, 10);
                while (counter <= l.YearTo) {
                    if (!raysLength[counter]) {
                        raysLength[counter] = [];
                    }
                    raysLength[counter].push(parseInt(l.Line, 10));
                    counter += 1;
                }
                const to = this.data.connectors.filter(c => c.LineTo === l.Line).map(c => (`t${c.LineFrom}`));
                const from = this.data.connectors.filter(c => c.LineFrom === l.Line).map(c => (`f${c.LineTo}`));
                lines.push({
                    key: `line-${l.ID}-${l.Line}-${l.YearFrom}`,
                    className: `alive ${to.join(' ')} ${from.join(' ')}`,
                    click: (line) => {
                        this.selectLine(line);
                    },
                    from: (START_ANGLE + ((l.YearFrom - MIN_YEAR) * YEAR_ANGLE)),
                    id: l.Line,
                    label: {
                        show: false,
                    },
                    radius: RAYS.hole[size] + (l.Line * LINE_HEIGHT[size]),
                    fill: LINES.fill.active[size],
                    height: VISIBLE_LINE_HEIGHT[size],
                    notes: l.Notes,
                    stroke: null,
                    strokeWidth: null,
                    strokeDasharray: null,
                    to: (START_ANGLE + ((l.YearTo - MIN_YEAR) * YEAR_ANGLE)),
                });
                if (addedGhosts.indexOf(l.Line) < 0) {
                    addedGhosts.push(l.Line);
                    const labelObj = this.data.logos.sort((a, b) => parseInt(b.YearTo, 10) - parseInt(a.YearTo, 10)).find(d => d.Line === l.Line)
                    let label = labelObj.Alias ? labelObj.Alias : (labelObj.Label ? labelObj.Label : null);
                    if (label) {
                        const number = this.data.logos.filter(d => d.Line === l.Line).length;
                        if (number > 1) {
                            label = `${label} (+${number})`;
                        }
                    }
                    ghosts.push({
                        key: `ghost-${l.ID}-${l.Line}-${l.YearFrom}`,
                        className: `ghost ${to.join(' ')} ${from.join(' ')}`,
                        click: (line) => {
                            this.selectLine(line);
                        },
                        from: (START_ANGLE + ((l.YearFrom - MIN_YEAR) * YEAR_ANGLE)),
                        id: l.Line,
                        radius: RAYS.hole[size] + (l.Line * LINE_HEIGHT[size]),
                        fill: 'transparent',
                        height: VISIBLE_LINE_HEIGHT[size],
                        label: {
                            click: () => { this.selectLine(l.Line) },
                            rotation: START_ANGLE + (((MAX_YEAR - MIN_YEAR)) * YEAR_ANGLE),
                            fill: RAYS.labels.fill,
                            fontFamily: LINES.labels.fontFamily,
                            fontSize: LINES.labels.fontSize[size],
                            fontWeight: LINES.labels.fontWeight,
                            show: label ? true : false,
                            text: label,
                        },
                        notes: l.Notes,
                        stroke: null,
                        strokeWidth: null,
                        strokeDasharray: null,
                        to: (START_ANGLE + ((MAX_YEAR - MIN_YEAR) * YEAR_ANGLE)),
                    });
                }
            });
            // Rays
            let lastRay = 0;
            for (let z = MIN_YEAR; z <= MAX_YEAR; z++) {
                // Rays, if the year is significant
                if (rawYears.indexOf(z) > -1) {
                    const rotation = START_ANGLE + (i * YEAR_ANGLE);
                    const rayRadius = (raysLength[z]) ? RAYS.hole[size] + (LINE_HEIGHT[size] * (Math.max(RAYS.minLines[size], raysLength[z].sort((a, b) => parseInt(b, 10) - parseInt(a, 10))[0]) + 1)) : 0;
                    if (rayRadius > 0) {
                        let show = false;
                        if (lastRay !== (z - 1) || size === 'XXXL') {
                            lastRay = z;
                            show = true;
                        }
                        rays.push({
                            key: `ray-${z}`,
                            innerRadius: RAYS.hole[size],
                            label: {
                                distance: RAYS.labels.distance,
                                fill: RAYS.labels.fill,
                                fontFamily: RAYS.labels.fontFamily,
                                fontSize: RAYS.labels.fontSize[size],
                                fontWeight: RAYS.labels.fontWeight,
                                show,
                                text: z,
                            },
                            outerRadius: rayRadius,
                            rotation,
                            stroke: RAYS.stroke,
                            strokeDasharray: RAYS.strokeDasharray,
                            strokeWidth: RAYS.strokeWidth,
                        });
                    }
                }
                i++;
            }
            // Connectors
            this.data.connectors.forEach((c) => {
                const direction = parseInt(c.LineFrom, 10) < parseInt(c.LineTo, 10) ? 'u' : 'd';
                const adj = ((size === 'S') ? 1 : 4);
                const fromToConnectors = this.data.connectors.filter(d => (parseInt(d.LineFrom, 10) === parseInt(c.LineTo, 10) && parseInt(d.LineTo, 10) === parseInt(c.LineFrom, 10)));
                const fromTo = fromToConnectors.length > 0 ? `f${c.LineTo}` : '';
                connectors.push({
                    adj,
                    key: `connector-${c.ID}`,
                    className: `t${c.LineTo} f${c.LineFrom} ${fromTo}`,
                    direction,
                    id: c.ID,
                    fill: CONNECTORS.fill,
                    from: RAYS.hole[size] + (c.LineFrom * LINE_HEIGHT[size]) + ((direction === 'u') ?  VISIBLE_LINE_HEIGHT[size] + adj : -adj),
                    properties: c,
                    rotation:  (START_ANGLE + ((c.Year - MIN_YEAR) * YEAR_ANGLE)),
                    size,
                    strokeWidth: CONNECTORS.strokeWidth,
                    strokeDasharray: CONNECTORS.strokeDasharray,
                    stroke: CONNECTORS.stroke,
                    to: RAYS.hole[size] + (c.LineTo * LINE_HEIGHT[size]) + ((direction === 'u') ? 0 : VISIBLE_LINE_HEIGHT[size]),
                });
            });
            this.setState({
                size,
                styles,
                svg: {
                    connectors,
                    epochs: epochsAreas,
                    ghosts,
                    lines,
                    rays,
                    visible: true,
                }
            });
        }
    }

    render() {
        if (this.state.svg.visible) {
            const translateValueX = CANVAS_SIZE[this.state.size].hd;
            const translateValueY = CANVAS_SIZE[this.state.size].vd;
            const { logo, year, label } = this.findLogo();
            const { selectedLogos, selectedHistory } = this.findDetails();
            console.log('this.findLogo()', this.findLogo());
            console.log('this.findDetails()', this.findDetails());
            
            return (
                <section className={`Timeline Timeline-selected-line-${this.state.selectedLine}`}>
                    <div className="timeline-select">
                        <SelectSearch
                            options={this.state.partiesList}
                            search
                            onChange={this.selectLine}
                            value={this.state.selectedLine}
                            name="language"
                            placeholder="Scegli un partito"
                            renderOption={this.renderLogos}
                        />
                    </div>
                    <div className="timeline-chart-container" style={{border: '1px solid gold'}}>
                        <svg
                            className="timeline-chart"
                            width={CANVAS_SIZE[this.state.size].w}
                            height={CANVAS_SIZE[this.state.size].h}
                            viewBox={`0 0 ${CANVAS_SIZE[this.state.size].w} ${CANVAS_SIZE[this.state.size].h}`}
                            preserveAspectRatio="xMidYMid meet"
                        >
                            <defs>
                                <pattern id="dots" x="0" y="0" width="3" height="3" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="1" height="1" fill="#bbb" />
                                </pattern>
                                <pattern id="dots2" x="0" y="0" width="4" height="4" patternUnits="userSpaceOnUse">
                                    <rect x="2" y="0" width="1" height="1" fill="#bbb" />
                                </pattern>
                                <pattern id="selected" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="2" fill="#f05656" />
                                </pattern>
                                <pattern id="full" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="2" fill="#f00" />
                                </pattern>

                                <pattern id="ghost-siblingFrom" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="1" fill={LINES.fill.ghostSiblingFrom.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.ghostSiblingFrom.S} />
                                </pattern>
                                <pattern id="ghost-siblingTo" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="1" fill={LINES.fill.ghostSiblingTo.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.ghostSiblingTo.S} />
                                </pattern>
                                <pattern id="ghost-siblingToFrom" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="1" fill={LINES.fill.ghostSiblingToFrom.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.ghostSiblingToFrom.S} />
                                </pattern>
                                <pattern id="ghost" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="1" fill={LINES.fill.ghost.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.ghost.S} />
                                </pattern>
                                <pattern id="line" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="2" height="1" fill={LINES.fill.active.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.active.S} />
                                </pattern>
                                <pattern id="inactive-line" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="1" height="2" fill={LINES.fill.inactive.S} />
                                    <rect x="1" y="1" width="1" height="1" fill={LINES.fill.inactive.S} />
                                </pattern>
                                <pattern id="sibling-lineFrom" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="1" height="2" fill={LINES.fill.siblingFrom.S} />
                                    <rect x="1" y="0" width="1" height="1" fill={LINES.fill.siblingFrom.S} />
                                </pattern>
                                <pattern id="sibling-lineTo" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="1" height="2" fill={LINES.fill.siblingTo.S} />
                                    <rect x="1" y="0" width="1" height="1" fill={LINES.fill.siblingTo.S} />
                                </pattern>
                                <pattern id="sibling-lineToFrom" x="0" y="0" width="2" height="2" patternUnits="userSpaceOnUse">
                                    <rect x="0" y="0" width="1" height="2" fill={LINES.fill.siblingToFrom.S} />
                                    <rect x="1" y="0" width="1" height="1" fill={LINES.fill.siblingToFrom.S} />
                                </pattern>

                            </defs>
                            <g className="epochs-background" transform={`translate(${translateValueX} ${translateValueY})`}>
                                {this.state.svg.epochs.map(r => (
                                    <Area
                                        key={r.key}
                                        fill={r.fill}
                                        from={r.from}
                                        innerRadius={r.innerRadius}
                                        label={r.label}
                                        outerRadius={r.outerRadius}
                                        stroke={r.stroke}
                                        strokeWidth={r.strokeWidth}
                                        strokeDasharray={r.strokeDasharray}
                                        to={r.to}
                                    />
                                ))}
                            </g>
                            <g className="rays" transform={`translate(${translateValueX} ${translateValueY})`}>
                                {this.state.svg.rays.map(r => (
                                    <Ray
                                        key={r.key}
                                        innerRadius={r.innerRadius}
                                        label={r.label}
                                        outerRadius={r.outerRadius}
                                        rotation={r.rotation}
                                        stroke={r.stroke}
                                        strokeWidth={r.strokeWidth}
                                        strokeDasharray={r.strokeDasharray} />
                                ))}
                            </g>
                            <g className="lines" transform={`translate(${translateValueX} ${translateValueY})`} style={{ mixBlendMode: 'multiply' }}>
                                {this.state.svg.ghosts.map(r => (
                                    <Arc
                                        key={r.key}
                                        className={r.className}
                                        click={r.click}
                                        from={r.from}
                                        id={r.id}
                                        label={r.label}
                                        radius={r.radius}
                                        fill={r.fill}
                                        height={r.height}
                                        notes={r.notes}
                                        to={r.to}
                                    />
                                ))}
                                {this.state.svg.lines.map(r => (
                                    <Arc
                                        key={r.key}
                                        className={r.className}
                                        click={r.click}
                                        from={r.from}
                                        id={r.id}
                                        label={r.label}
                                        radius={r.radius}
                                        fill={r.fill}
                                        height={r.height}
                                        notes={r.notes}
                                        to={r.to}
                                    />
                                ))}
                            </g>
                            <g className="connectors" transform={`translate(${translateValueX} ${translateValueY})`}>
                                {this.state.svg.connectors.map(r => (
                                    <Connector
                                        adj={r.adj}
                                        key={r.key}
                                        className={r.className}
                                        direction={r.direction}
                                        fill={r.fill}
                                        from={r.from}
                                        id={r.id}
                                        properties={r.properties}
                                        rotation={r.rotation}
                                        size={r.size}
                                        strokeWidth={r.strokeWidth}
                                        strokeDasharray={r.strokeDasharray}
                                        stroke={r.stroke}
                                        to={r.to} />
                                ))}
                            </g>
                            <g className="legend">
                                <text
                                    x="20"
                                    y={LEGEND.top[this.state.size]}
                                    fill={LEGEND.title.fill}
                                    fontFamily={LEGEND.title.fontFamily}
                                    fontSize={LEGEND.title.fontSize[this.state.size]}
                                    fontWeight={LEGEND.title.fontWeight}
                                    style={LEGEND.title.style}>Legenda</text>
                                {LEGEND.items.map((l,i) => (
                                    <g key={l.label}>
                                        <rect
                                            x="20"
                                            y={LEGEND.top[this.state.size] + 13 + (20 * i)}
                                            width="10"
                                            height="10"
                                            fill={l.fill[this.state.size]} />
                                        <text
                                            x="40"
                                            y={LEGEND.top[this.state.size] + 20 + (20 * i)}
                                            fill={LEGEND.fill}
                                            fontFamily={LEGEND.fontFamily}
                                            fontSize={LEGEND.fontSize[this.state.size]}
                                            fontWeight={LEGEND.fontWeight}
                                            style={LEGEND.style}>{l.label}</text>
                                    </g>
                                ))}
                            </g>
                            <g transform={`translate(${translateValueX} ${translateValueY})`}>
                                <circle
                                    cx="0"
                                    cy="0"
                                    r={RAYS.hole[this.state.size]}
                                    fill="none"
                                    stroke="#bbb"
                                    strokeWidth="2"
                                />
                            </g>
                        </svg>
                        <img
                            className="logo"
                            src={logo}
                            alt="Logo"
                            style={{ top: `${translateValueY}px`, left: `${translateValueX}px`, height: `${(RAYS.hole[this.state.size] * 2) - 5}px`, width: `${(RAYS.hole[this.state.size] * 2) - 5}px` }}
                        />
                        {label && (
                            <div
                                className="year"
                                style={{ top: `${translateValueY + RAYS.hole[this.state.size]}px`, left: `${translateValueX}px`}}>{year}</div>
                        )}
                    </div>
                    {selectedHistory && (
                        <div className="details">
                            <Details
                                assets={ASSETS_URL}
                                click={this.selectLine}
                                logos={selectedLogos}
                                history={selectedHistory}
                            />
                        </div>
                    )}
                    <style type="text/css">
                        {this.state.styles}
                    </style>
                </section>
            );
        } else {
            return (<div>Chart not ready</div>);
        }
    }
}

export default Timeline;