import React, {Component} from 'react';
import {arc} from 'd3-shape';

class Area extends Component {
  render() {
    const {fill, from, to, innerRadius, outerRadius, label, stroke, strokeWidth, strokeDasharray} = this.props;
    const rotation = (2 * Math.PI) - (Math.PI / 2);
    const rad1 = (from * Math.PI / 180) - rotation;
    const rad2 = (to * Math.PI / 180) - rotation;
    const arcPath = arc()
        .innerRadius(innerRadius)
        .outerRadius(outerRadius)
        .startAngle(rad1)
        .endAngle(rad2);
    const labelObj = {
      show: false,
    };
    let textPath = '';
    if (label.show) {
      const rad3radius = outerRadius + 10
      const rad3 = ((180 + from + 45) * Math.PI / 180);
      const rad4 = ((180 + to - 45) * Math.PI / 180);
      textPath = `M${rad3radius * Math.cos(rad3)} ${rad3radius * Math.sin(rad3)} A${rad3radius},${rad3radius} 1 1,1 ${rad3radius * Math.cos(rad4)},${rad3radius * Math.sin(rad4)}`;
      labelObj.alignmentBaseline = 'middle';
      labelObj.dominantBaseline = 'middle';
      labelObj.fill = label.fill;
      labelObj.fontFamily = label.fontFamily;
      labelObj.fontSize = label.fontSize;
      labelObj.fontWeight = label.fontWeight;
      labelObj.show = true;
      labelObj.text = label.text;
      labelObj.textAnchor = 'middle';
      labelObj.transform = null;
    }
    return (
      <g className="epoch">
        {label.show && (
          <path
            d={textPath}
            id={`arc-${to}-${from}`}
            stroke="none"
            fill="none"
            strokeWidth="1"
          />
        )}
        <path
          d={arcPath()}
          className="epoch-path"
          fill={fill}
          stroke={stroke}
          strokeWidth={strokeWidth}
          strokeDasharray={strokeDasharray} />
          {label.show && (
            <text
              alignmentBaseline={labelObj.alignmentBaseline}
              className="epoch-label"
              dominantBaseline={labelObj.dominantBaseline}
              fill={labelObj.fill}
              fontFamily={labelObj.fontFamily}
              fontSize={labelObj.fontSize}
              fontWeight={labelObj.fontWeight}
              textAnchor={labelObj.textAnchor}
              transform={labelObj.transform}>
                <textPath
                  href={`#arc-${to}-${from}`}
                  startOffset="50%"
                >{labelObj.text}</textPath>
              </text>
          )}
      </g>
    );
  }
}

export default Area;