import React, {Component} from 'react';

class Connector extends Component {
  render() {
    const {strokeWidth, strokeDasharray, stroke, rotation, fill, className, direction, adj} = this.props;
    let { from } = this.props;
    
    const rad = (rotation) * Math.PI / 180;
    
    const x1 = from * Math.cos(rad);
    const y1 = from * Math.sin(rad);
    const x2l1 = x1 + adj;
    const x2l2 = x1 - adj;
    const y2l1 = y1 + adj;
    const y2l2 = y1 + adj;
    const r = rotation + ((direction === 'u') ? 90 : -90);
    
    return (
      <g className={`connector ${className}`}>
          <g transform={`rotate(${r} ${x1} ${y1})`}>
            <path
              className='connector-arrow'
              d={`M${x1},${y1} L${x2l1},${y2l1} L${x2l2},${y2l2} Z`}
              fill={fill}
              stroke={stroke}
              strokeWidth={strokeWidth}
              strokeDasharray={strokeDasharray} />
          </g>
      </g>
    );
  }
}

export default Connector;

