import React, {Component} from 'react';

class Ray extends Component {
  render() {
    const {strokeWidth, strokeDasharray, stroke, rotation, innerRadius, outerRadius, label} = this.props;
    const rad = (rotation) * Math.PI / 180;
    const x1 = innerRadius * Math.cos(rad);
    const y1 = innerRadius * Math.sin(rad);
    const x2 = outerRadius * Math.cos(rad);
    const y2 = outerRadius * Math.sin(rad);
    const labelObj = {};
    if (label.show) {
      const x = (outerRadius + label.distance) * Math.cos(rad);
      const y = (outerRadius + label.distance) * Math.sin(rad);
      labelObj.alignmentBaseline = 'middle';
      labelObj.dominantBaseline = 'middle';
      labelObj.fill = label.fill;
      labelObj.fontFamily = label.fontFamily;
      labelObj.fontSize = label.fontSize;
      labelObj.fontWeight = label.fontWeight;
      labelObj.text = label.text;
      labelObj.textAnchor = (rad > 4.9) ? 'start' : 'end';
      labelObj.transform = `rotate(${(rad > 4.9) ? rotation : 180 + rotation} ${x} ${y})`;
      labelObj.x = x;
      labelObj.y = y;
    }
    return (
      <g>
        <line
            x1={x1}
            y1={y1}
            x2={x2}
            y2={y2}
            className="ray"
            stroke={stroke}
            strokeWidth={strokeWidth}
            strokeDasharray={strokeDasharray} />
        {label.show && (
          <text
            alignmentBaseline={labelObj.alignmentBaseline}
            className="ray-label"
            dominantBaseline={labelObj.dominantBaseline}
            fill={labelObj.fill}
            fontFamily={labelObj.fontFamily}
            fontSize={labelObj.fontSize}
            fontWeight={labelObj.fontWeight}
            textAnchor={labelObj.textAnchor}
            transform={labelObj.transform}
            x={labelObj.x}
            y={labelObj.y}>{labelObj.text}</text>
        )}
      </g>
    );
  }
}

export default Ray;

