import React, {Component} from 'react';
import ReactMarkdown from 'react-markdown';
import './Details.css';

class Details extends Component {
  constructor(props) {
    super(props);

    this.clickOnLink = this.clickOnLink.bind(this);
    this.hideGallery = this.hideGallery.bind(this);
    this.prepareLink = this.prepareLink.bind(this);
    this.showGallery = this.showGallery.bind(this);

    this.RENDERERS = {
      link: (tag) => {
        if (tag.href.indexOf('linkTo:') === 0) {
          const lineID = tag.href.replace(/linkTo:/ig, '');
          return <a href={tag.href} className="details-internal-link" onClick={(e) => { this.clickOnLink(e, lineID); }} title={tag.children[0].props.value}>{tag.children[0].props.value}</a>
        }
        return <a className="details-external-link" href={tag.href} target="_ext" title={tag.children[0].props.value}>{tag.children[0].props.value}</a>
      }
    }

    this.state = {
      image: {},
      showImage: false,
    };
  }

  clickOnLink(e, lineID) {
    e.preventDefault();
    if (lineID && typeof this.props.click === 'function') {
      this.props.click(lineID);
    }
  }

  hideGallery() {
    this.setState({
      imageInfo: {},
      showImage: false,
    });
  }

  prepareLink(u) {
    return u;
  }

  showGallery(imageInfo) {
    this.setState({
      imageInfo,
      showImage: true,
    });
  }
  
  render() {
    const { logos, history, assets } = this.props;
    const { showImage, imageInfo } = this.state;
    const newLogos = logos.map((l) => ({
      key: 'i',
      images:[ { url: `${assets}/${l.Logo}.png`, caption: null} ],
      logo: true,
      year: parseInt(l.YearFrom),
    }));
    const timeline = history.timeline.concat(newLogos).sort((a, b) => a.year - b.year);

    return (
      <div className="Details">
        <h1>
          <ReactMarkdown source={history.title} />
        </h1>
        <div className="details-description">
          <ReactMarkdown source={history.text} renderers={this.RENDERERS} transformLinkUri={this.prepareLink} />
        </div>
        <h2>
          La storia del partito
        </h2>
        <ul className="details-timeline-items">
          {timeline.map((l, i) => (
            <li key={`${l.year}-${l.key || 't'}-${i}`} className="details-timeline-item">
              <div className="details-timeline-item-year">
                {!l.logo && (<h3>{l.year}</h3>)}
                {l.logo && (<span> </span>)}
              </div>
              <div className="details-timeline-item-content">
                {l.text && (
                  <div className="details-timeline-item-text">
                    <ReactMarkdown source={l.text} renderers={this.RENDERERS} transformLinkUri={this.prepareLink} />
                  </div>
                )}
                {l.images && l.images.length > 0 && (
                  <div className={`details-timeline-item-images ${l.images.length > 1 ? 'gallery' : ''}`}>
                    {l.images.map(i => (
                      <div
                        key={i.url}
                        className="details-timeline-item-image"
                        onClick={(e) => { e.preventDefault(); if (l.images.length > 1) { this.showGallery(i); } }}>
                          <figure>
                            <img src={`${i.url}`} alt="" />
                            {i.caption && (
                              <figcaption>{i.caption}</figcaption>
                            )}
                          </figure>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </li>
          ))}
        </ul>
        {history.notables.length > 0 && (
          <div>
            <h2>
              Personalità di rilievo
            </h2>
            <ul className="notables">
            {history.notables.sort((a, b) => ((a.name > b.name) ? 1 : -1)).map(n => (
              <li
                className="notable"
                key={n.name}>
                  <a className="details-external-link" href={n.link} target="_ext">
                    {n.image && (
                      <figure>
                        <img src={n.image} alt={n.name} title={n.name} />
                      </figure>
                    )}
                    {n.name}
                  </a>
              </li>
            ))}
            </ul>
          </div>
        )}
        {history.links.length > 0 && (
          <div>
            <h2>
              Maggiori informazioni
            </h2>
            <ul>
            {history.links.map(n => (
              <li
                key={n.title}>
                  <a className="details-external-link" href={n.link} target="_ext">{n.title}</a>
              </li>
            ))}
            </ul>
          </div>
        )}
        {showImage && (
          <div className="details-lightbox">
            <div className="details-lightbox-inner">
              <div className="details-lightbox-close">
                <span role="button" onClick={this.hideGallery}>Chiudi</span>
              </div>
              <div className="details-lightbox-content">
                <figure>
                  <img src={imageInfo.url} alt={imageInfo.caption} />
                  {imageInfo.caption && (
                    <figcaption>
                      {imageInfo.caption}
                    </figcaption>
                  )}
                </figure>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default Details;

