import React, {Component} from 'react';
import {arc} from 'd3-shape';

class Arc extends Component {
  render() {
    const { id, radius, height, className, label, click } = this.props;
    const onClick = (e) => {
      e.preventDefault();
      if (typeof click === 'function') {
        click(id);
      }
    };
    let { from, to, fill, stroke, strokeWidth, strokeDasharray } = this.props;
    if (from === to) {
      to += 1;
      from -= 1;
    }
    const rotation = (2 * Math.PI) - (Math.PI / 2);
    const rad1 = (from * Math.PI / 180) - rotation;
    const rad2 = (to * Math.PI / 180) - rotation;
    const arcPath = arc()
        .innerRadius(radius)
        .outerRadius(radius + height)
        .startAngle(rad1)
        .endAngle(rad2);
    const labelObj = {};
    const circleObj = {};
    if (label.show) {
      const x = (radius + height / 2) * Math.cos(((label.rotation) * Math.PI / 180));
      const y = (radius + height / 2) * Math.sin(((label.rotation) * Math.PI / 180));
      labelObj.alignmentBaseline = 'middle';
      labelObj.dominantBaseline = 'middle';
      labelObj.fill = label.fill;
      labelObj.fontFamily = label.fontFamily;
      labelObj.fontSize = label.fontSize;
      labelObj.fontWeight = label.fontWeight;
      labelObj.text = label.text;
      labelObj.textAnchor = 'end';
      labelObj.transform = `rotate(${270 + label.rotation} ${x} ${y})`;
      labelObj.x = x - 20;
      labelObj.y = y;

      circleObj.cx = x;
      circleObj.cy = y;
      circleObj.r = height / 1.5;
      circleObj.fill = label.fill;
    }

    return (
      <g className={`arc arc-${className} l${id}`}>
          <path
            d={arcPath()}
            className={`line line-${className}`}
            onClick={onClick}
            fill={fill}
            stroke={stroke}
            strokeWidth={strokeWidth}
            strokeDasharray={strokeDasharray} />
          {label.show && (
            <g>
              <text
                alignmentBaseline={labelObj.alignmentBaseline}
                className={`line-label line-label-${className}`}
                onClick={onClick}
                dominantBaseline={labelObj.dominantBaseline}
                fill={labelObj.fill}
                fontFamily={labelObj.fontFamily}
                fontSize={labelObj.fontSize}
                fontWeight={labelObj.fontWeight}
                textAnchor={labelObj.textAnchor}
                transform={labelObj.transform}
                x={labelObj.x}
                y={labelObj.y}>{labelObj.text}</text>
            </g>
          )}
      </g>
    );
  }
}

export default Arc;

